.sidebar-panel {
  background: linear-gradient(180deg, #f9fcff 0%, #eef4fb 100%);
  border: 1px solid #d6deea;
  border-left: 0;
  box-shadow: 0 14px 28px rgba(15, 23, 42, 0.1);
}

.sidebar-scroll {
  background: transparent;
}

.sidebar-menu-title {
  color: #1e293b;
  border-color: #d6deea;
  letter-spacing: 0.02em;
}

.sidebar-menu-list {
  color: #334155;
}

.sidebar-panel a,
.sidebar-panel li,
.sidebar-panel span,
.sidebar-panel div[role="button"] {
  transition: background-color 0.18s ease, color 0.18s ease;
}

.sidebar-panel a:hover,
.sidebar-panel li:hover {
  color: #1d4ed8;
}

.sidebar-logo-link img {
  filter: drop-shadow(0 4px 10px rgba(15, 23, 42, 0.15));
}

.sidebar-toggle-btn {
  background: #e7eef8;
  border: 1px solid #c9d4e6;
  color: #1e293b;
}

.sidebar-toggle-btn:hover {
  background: #dbe7f7;
}
