.app-footer {
  background: linear-gradient(180deg, #f8fbff 0%, #eef4fb 100%);
  border-top: 1px solid #d6deea;
  box-shadow: 0 -6px 20px rgba(15, 23, 42, 0.06);
}

.app-footer-text {
  color: #475569;
  font-weight: 500;
  letter-spacing: 0.01em;
}

.app-footer-link {
  color: #1d4ed8;
  font-weight: 700;
}

.app-footer-link:hover {
  color: #1e3a8a;
}
