.app-header {
  --header-ink: #0f172a;
  --header-muted: #475569;
  --header-border: #d7e0ec;
  --header-surface: linear-gradient(180deg, #f8fbff 0%, #eef4fb 100%);
  --header-accent: #1d4ed8;
  --header-accent-soft: #dbeafe;
  font-family: "Pretendard", "Noto Sans KR", "Segoe UI", sans-serif;
  background: var(--header-surface);
  border-bottom: 1px solid var(--header-border);
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.08);
}

.header-top-row {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  padding: 0.9rem 1.4rem 0;
}

.header-user-actions {
  display: inline-flex;
  align-items: center;
  gap: 0.8rem;
  padding: 0.35rem 0.7rem;
  border: 1px solid var(--header-border);
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(4px);
}

.header-user-name {
  color: var(--header-muted);
  font-size: 0.82rem;
  font-weight: 600;
  white-space: nowrap;
}

.header-mypage-link,
.header-logout-btn {
  font-size: 0.82rem;
  font-weight: 700;
  white-space: nowrap;
  transition: 0.2s ease;
}

.header-mypage-link {
  color: var(--header-accent);
}

.header-mypage-link:hover {
  color: #1e40af;
}

.header-logout-btn {
  background: transparent;
  border: 0;
  color: #b91c1c;
  cursor: pointer;
}

.header-logout-btn:hover {
  color: #7f1d1d;
}

.header-nav {
  padding: 1rem 0 1rem;
}

.header-nav-inner {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0 1rem;
}

.header-category-wrap {
  width: min(1100px, 100%);
  display: flex;
  text-align: center;
  border: 1px solid var(--header-border);
  border-radius: 14px;
  overflow: visible;
  background: rgba(255, 255, 255, 0.92);
}

.header-category-item {
  flex: 1;
  position: relative;
  cursor: pointer;
  color: var(--header-ink);
  font-weight: 800;
  font-size: clamp(1rem, 1.55vw, 1.2rem);
  letter-spacing: 0.04em;
  padding: 0.75rem 0.6rem;
  border-right: 1px solid var(--header-border);
  transition: 0.2s ease;
}

.header-category-item:last-child {
  border-right: 0;
}

.header-category-item:hover {
  color: var(--header-accent);
  background: linear-gradient(180deg, #f8fbff 0%, var(--header-accent-soft) 100%);
}

.header-submenu {
  position: absolute;
  top: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%);
  min-width: 210px;
  border: 1px solid var(--header-border);
  border-radius: 12px;
  background: #ffffff;
  box-shadow: 0 18px 30px rgba(15, 23, 42, 0.16);
  z-index: 40;
  overflow: hidden;
}

.header-submenu-link {
  display: block;
  padding: 0.65rem 0.9rem;
  color: #1f2937;
  font-size: 0.88rem;
  font-weight: 700;
  letter-spacing: 0.02em;
  text-align: left;
  transition: 0.16s ease;
}

.header-submenu-link:hover {
  color: #1e3a8a;
  background: #eff6ff;
}

@media (max-width: 900px) {
  .header-top-row {
    padding: 0.7rem 0.8rem 0;
  }

  .header-user-actions {
    gap: 0.55rem;
    padding: 0.3rem 0.5rem;
  }

  .header-category-wrap {
    border-radius: 10px;
  }

  .header-category-item {
    font-size: 0.94rem;
    padding: 0.6rem 0.32rem;
    letter-spacing: 0.01em;
  }

  .header-submenu {
    min-width: 175px;
  }
}
